body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.developers-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.developer-card {
  width: 300px;
  height: 400px;
  box-sizing: border-box;
  margin: 30px;
  padding: 30px;
  background-color: #3f51b5;
  border-radius: 30px;
  color: aliceblue;
}

.developer-card h2 {
  text-align: center;
}

.developer-card img{
  border-radius: 10px; 
}

.developer-card a{
  display: flex;
  justify-content: center;
  text-decoration: none;
  color: aliceblue;
}

.developer-card svg{
  margin: 0 5px; 
}

.developer-card a:hover{
  color: red;
}

@media screen and (max-width: 1199px) {
  .youtube-video {
    width: 80vw;
    height: 50vw;
  }
}

@media screen and (min-width: 1200px) {
  .youtube-video {
    width: 50vw;
    height: 30vw;
  }

#map {
  width: 640px;
  height: 480px;
  max-width: 85vw;
  max-height: 40vh;
  }
}

@media screen and (max-width: 1199px) {
  #map {
    width: 92vw;
  }
}

@media screen and (min-width: 1200px) {
  #map {
    width: 50vw;
  }
}

h1 {
  font-size: 2.5rem;
  text-align: center;
  line-height: 3rem;
}

.App > article {
  box-sizing: border-box;
  padding: 10px;

}

.App > article > p {
  text-align: justify;
  text-indent: 1.5rem;
}

.image-gallery {
width: 600px;
max-width: 80vw;
}

.image-gallery-content:not(.fullscreen) .image-gallery-image {
  width: 100%;
  max-width: 80vw;
  height: 0;
  padding-top: 75%; /* Make the image wrapping div take a ratio of 3:4 */
  position: relative;
}

.image-gallery-content:not(.fullscreen) .image-gallery-image img {
  position: absolute;
  top: 0;
  object-fit: contain;
  object-position: center center;
  width: 100%;
  height: 100%;
}

#map {
  height: 50vh;
}

.timeline time {
  background: #3f51b5;
}

